@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.headerBarWrapper {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.headerBarWrapper .headerBarLeft {
  height: 50px;
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: center;
}
.headerBarWrapper .headerBarLeft span {
  font-family: "NotoSansHans-Medium", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 22px;
  font-weight: 500;
  line-height: 22px;
  color: #000000;
  text-align: left;
  margin-left: 14px;
  white-space: nowrap;
}
.headerBarWrapper .headerBarLeft div {
  height: 45px;
  width: 45px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/headbar/logo1.png') left no-repeat;
  background-size: contain;
}
.headerBarWrapper .headerBarLeft div:hover {
  cursor: pointer;
}
@media screen and (min-width: 524px) {
  .ant-layout-header {
    padding: 0px 30px !important;
  }
}
@media screen and (max-width: 524px) {
  .ant-layout-header {
    padding: 0px 10px !important;
  }
}
